<template>
	<div>
		<ul class="farminglist" v-if="missionList.length">
			<li class="farmingitem" v-for="mission in missionList" :key="mission.id">
				<div class="line"></div>
				<div class="farm-info">
					<p class="title">{{ mission.ftype | fMissionName }}</p>
					<p class="info">
						<span class="time" v-if="mission.subItem.completeTime">{{ mission.subItem.completeTime | znDateFormat("yyyy.MM.dd") }}</span>
						<span class="person" v-if="mission.subItem.worker || mission.subItem.floodingMan"><img src="../../assets/images/gismap/icon-1.png" alt="负责人logo" />{{ mission.subItem.worker || mission.subItem.floodingMan }}</span>
					</p>
          <!-- 投入品只显示 施肥和用药 -->
					<div class="source-list" v-if="showMissionInputList(mission.ftype)">
						<div class="source">
							<p class="description">
								<span class="name">{{ mission.subItem.name }}</span>
								<span class="specification" v-if="mission.subItem.amount">{{ mission.subItem.amount }} {{ mission.ftype === '6' ? '克/毫升' : '公斤' }}</span>
								<span class="factory" v-if="mission.subItem.remark || mission.subItem.supplyCompany ">厂家：{{ mission.ftype === '6' ? mission.subItem.remark : mission.subItem.supplyCompany }}</span>
							</p>
						</div>
					</div>
				</div>
			</li>
		</ul>
		<div class="nodata" v-else-if="!missionList.length">
			<img src="../../assets/images/no_inputs.png" alt="无数据" />
			<p>暂无农事记录</p>
		</div>
	</div>
</template>
<script>
export default {
  data () {
    return {
    };
  },
  props: {
    missionList: {
      type: Array,
      default () {
        return [];
      }
    }
  },
  created () {},
  methods: {
    showMissionInputList (ftype) {
      ftype = Number(ftype);
      return ftype === 1 || ftype === 6;
    }
  },
  computed: {
  },
  filters: {
    fMissionName (ftype) {
      switch (Number(ftype)) {
        case 0:
          return '种植';
        case 1:
          return '施肥';
        case 2:
          return '灌溉';
        case 3:
          return '其他农事';
        case 4:
          return '采收';
        case 5:
          return '销售';
        case 6:
          return '用药';
        default:
          return '未知';
      }
    }
  },
  components: {
  }
};
</script>
<style scoped lang="scss">
@mixin flex($x: center, $y: center) {
	display: flex;
	display: -webkit-flex;
	flex-flow: row nowrap;
	justify-content: $x;
	align-items: $y;
}

@mixin fontS($s: 14px) {
	font-size: $s;
}

.farminglist {
	padding: 0 35px 0 40px;
	.farmingitem {
		@include flex(flex-start, flex-start);
		position: relative;
		.line {
			width: 2px;
			height: 100%;
			background: #19aeb3;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			&:after {
				content: '.';
				font-size: 0;
				display: inline-block;
				width: 10px;
				height: 10px;
				background-color: #19aeb3;
				border-radius: 50%;
				position: absolute;
				top: -4px;
				left: -4px;
			}
		}
		&:last-child .line {
			bottom: auto;
			height: 5px;
		}
		.farm-info {
			width: 605px;
			background: #061f36;
			margin-bottom: 12px;
			margin-left: 28px;
			padding: 10px;
			box-sizing: border-box;
			text-align: left;
			&:after {
				content: '.';
				font-size: 0;
				display: inline-block;
				width: 0;
				height: 0;
				border-top: 0 solid transparent;
				border-bottom: 16px solid transparent;
				border-right: 16px solid #061f36;
				position: absolute;
				top: 0px;
				left: 12px;
			}
			p.title {
				line-height: 24px;
				margin: 10px 0;
				font-size: 16px;
				font-weight: bold;
				color: #a1eff8;
				text-align: left;
			}
			p.info {
				line-height: 18px;
				text-align: left;
				span.time {
					display: inline-block;
					padding: 0 4px;
					line-height: 18px;
					background: #06567c;
					border-radius: 4px;
					font-size: 14px;
					color: #6dc1cb;
				}
				span.person {
					font-size: 12px;
					color: #6dc1cb;
					float: right;
					img {
						vertical-align: -5px;
						margin-right: 2px;
					}
				}
			}
			.source-list {
				width: 100%;
				overflow: hidden;
				p.description {
					width: 100%;
					line-height: 30px;
					padding: 0 11px;
					box-sizing: border-box;
					background: #051423;
					border-radius: 4px;
					text-align: left;
					font-size: 14px;
					color: #6dc1cb;
					margin-top: 10px;
					overflow: hidden;
					// white-space: nowrap;
					// text-overflow: ellipsis;
					span {
						display: inline-block;
						float: left;
						max-width: 240px;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
					}
					span.name {
						min-width: 90px;
						color: #6dc1cb;
						margin-right: 15px;
					}
					span.specification {
						display: inline-block;
						max-width: 110px;
						color: #28737c;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
					}
					span.factory {
						display: inline-block;
						max-width: 180px;
						color: #28737c;
						float: right;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
					}
				}
			}
			.farming-imgs {
				width: 100%;
				img {
					display: inline-block;
					width: 60px;
					height: 60px;

					border-radius: 8px;
					overflow: hidden;
					margin: 10px 10px 0 0;
					cursor: pointer;
				}
			}
		}
	}
}

.nodata {
	height: 460px;
	@include flex(center, center);
	flex-flow: column;

	p {
		color: #6dc1cb;
		@include fontS(20px);
		line-height: 42px;
    margin-bottom: 30px;
	}
}

.loading /deep/ .el-loading-mask {
	background-color: rgba(0, 0, 0, 0);
	padding-top: 200px;
}
</style>



